body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: black;
  font-family: Arial, sans-serif;
  color: white;
}

#player-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#video-player {
  width: 103%;
  height: 103%;
  object-fit: cover;
}

#channel-list {
  position: fixed;
  top: 0;
  left: -300px; /* Hidden initially */
  width: 300px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  overflow-y: auto;
  transition: left 0.3s;
  font-size: 16px; /* Change the font size */
}

#channel-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

#channel-list li {
  padding: 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

#channel-list li img {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

#channel-list li:hover,
#channel-list li.selected {
  background-color: rgba(255, 255, 255, 0.219);
}

#epg {
  position: fixed;
  top: 0;
  right: -300px;
  width: 300px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  overflow-y: auto;
  transition: right 0.3s;
  font-size: 14px;
}

#epg-header {
  background-color: #444;
  padding: 10px;
  text-align: center;
  font-size: 18px;
}

#epg-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

#epg-list li {
  padding: 10px;
  border-bottom: 1px solid #555;
}
